.social {
  display: flex;
}

.social__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 6px 0;

  list-style: none;
}

.social__item {
  display: flex;
  margin-bottom: 21px;

  span {
    margin-left: 15px;
  }

  &:first-child span {
    margin-left: 19px;
  }

  &:first-child svg {
    margin-left: 3px;
  }
}

.social__link {
  display: flex;
  align-items: center;

  &:hover {
    color: $color-brown;
  }

  &:active {
    text-decoration: underline;
  }
}
