.main-nav {
  display: flex;
  justify-content: center;
  margin-top: 7px;

  font-size: 16px;
  line-height: 24px;

  @media (max-width: $tablet-width) {
    justify-content: flex-start;
    font-size: 24px;
    line-height: 24px;
    margin-top: 44px;
  }
}

.main-nav__list {
  display: grid;
  margin: 0;
  margin-left: 10px;
  padding: 0;

  list-style: none;

  grid-gap: 15px;
  grid-template-columns: repeat(5, min-content);

  @media (max-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-left: 0;
  }
}

.main-nav__item {
  display: flex;

  justify-self: center;

  &:last-child {
    margin-right: 0;
  }
}

.main-nav__link {
  display: flex;
  padding: 10px;

  &:hover {
    color: $color-brown;
  }

  &:active {
    text-decoration: underline;
  }

  @media (max-width: $tablet-width) {
    padding: 9px 0;
  }
}
