.about {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.about__list {
  width: 100%;
  margin: 0;
  padding: 6px;

  list-style: none;
  column-count: 2;
  column-gap: 12%;

  @media (max-width: $tablet-width) {
    padding: 6px 0;
    column-gap: 16%;
  }
}

.about__item {
  width: max-content;
  margin-bottom: 21px;
}

.about__link {
  display: flex;

  &:hover {
    color: $color-brown;
  }

  &:active {
    text-decoration: underline;
  }
}
