.slider {
  position: relative;

  width: 100%;

  font-style: normal;
  font-family: $font-family--poppins;
  text-align: center;
}

.slider__arrow {
  position: absolute;

  width: 30px;
  height: 20px;
  padding: 0;

  background-color: transparent;
  background-image: url("../img/svg/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: none;
  cursor: pointer;

  &:active {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width) {
    width: 38px;
    height: 38px;
  }
}

.slider__arrow--disabled {
  cursor: none;
  opacity: 0.3;

  pointer-events: none;
}

.slider__arrow--prev {
  top: -109px;
  left: 0;

  transform: rotate(-180deg);
}

.slider__arrow--next {
  top: -109px;
  right: 0;
}

.slider__window {
  margin-bottom: 47px;
  overflow-x: hidden;

  user-select: none;

  touch-action: pan-y;

  @media (max-width: $tablet-width) {
    margin-bottom: 37px;
  }
}

.slider__list {
  display: flex;
  width: 0;
  margin: 0;
  padding: 0;

  list-style: none;

  transition: 0.6s;
}

.slider__list.no-js {
  overflow: auto;
  width: 100%;
}

.slider__item {
  display: flex;
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }
}

.slider__link {
  width: 270px;
  display: flex;
  flex-direction: column;
  padding-bottom: 5px;

  color: $color-special-black;

  &:hover {
    color: $color-brown;
  }

  &:active {
    text-decoration: underline;
  }

  &:focus {
    outline-offset: -2px;
  }

  @media (max-width: $tablet-width) {
    width: 324px;
  }
}

.slider__picture img {
  width: 100%;
  margin-bottom: 21px;

  pointer-events: none;
}

.slider__description {
  display: flex;
  justify-content: center;
  margin-bottom: 6px;

  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
}

.slider__price {
  display: flex;
  justify-content: center;

  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
}

.slider__pagination {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.slider__button-pagination--tablet {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
  }
}

.slider__button-pagination {
  margin-right: 3px;
  padding: 2px 3px;

  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  color: $color-grey;

  background-color: transparent;
  border: none;
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    text-decoration: underline;
  }

  @media (max-width: $tablet-width) {
    margin-right: 7px;
  }
}

.slider__button-pagination--active {
  color: $color-special-black;
}
