.subscription {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin-right: 25px;

  @media (max-width: $tablet-width) {
    max-width: 560px;
  }
}

.subscription__title {
  margin: 0;
  margin-bottom: 9px;

  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  font-family: $font-family--cormorant-garamond;
  letter-spacing: 0.02em;
}

.subscription__description {
  margin: 0;
  margin-bottom: 35px;
  padding: 0;

  font-weight: 400;
  color: $color-grey;
}

.subscription__form {
  border-bottom: 1px solid $color-white;

  form {
    display: flex;
    width: 100%;

    label {
      width: 79.5%;

      @media (max-width: $tablet-width) {
        width: 82%;
      }
    }
  }

  input {
    width: 100%;
    padding: 10px 0;

    background-color: transparent;
    border: none;

    &::placeholder {
      font-size: 14px;
      line-height: 21px;
      color: $color-grey;
    }
  }

  button {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 20.5%;
    padding: 10px 0;

    color: $color-white;

    background-color: transparent;
    border: none;
    cursor: pointer;

    &:hover {
      color: $color-brown;
    }

    &:active {
      text-decoration: underline;
    }

    @media (max-width: $tablet-width) {
      width: 18%;
    }
  }
}
