.additional-menu {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    align-items: center;
    padding-top: 28px;

    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    font-family: $font-family--poppins;
    color: rgba($color-black, 0.75);
  }
}

.additional-menu__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;

  list-style: none;
}

.additional-menu__link {
  display: flex;
  padding: 10px 0;

  &:hover {
    color: $color-brown;
  }

  &:active {
    text-decoration: underline;
  }
}

.additional-menu__link--login {
  padding: 36px 0;
  color: $color-black;
}
