.header {
  width: 100%;
  margin: 0;
  padding-top: 30px;
  padding-bottom: 9px;

  font-style: normal;
  font-weight: 500;
  font-family: $font-family--poppins;
  color: $color-special-black;

  @media (max-width: $tablet-width) {
    padding-bottom: 11px;
  }
}

.header__upper-block-wrapper {
  padding-bottom: 19px;

  border-bottom: 1px solid $color-special-white;

  @media (max-width: $tablet-width) {
    border-bottom: none;
  }
}

.header__upper-block {
  position: relative;

  display: grid;

  font-size: 14px;
  line-height: 21px;

  grid-template-columns: repeat(3, 1fr);
}

.header__lower-block {
  @media (max-width: $tablet-width) {
    padding-top: 20px;
    padding-bottom: 29px;
  }
}

.header__lower-block.block-close {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header__toggle-menu {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    align-self: center;
    width: 25px;
    height: 18px;

    background-color: transparent;
    background-image: url("../img/svg/open-menu.svg");
    border: none;
    cursor: pointer;
  }
}

.header__form-desktop {
  display: flex;
  width: 174px;
  margin-top: 9px;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header__form-desktop label {
  display: flex;
  align-items: center;

  &:hover {
    color: $color-brown;
  }
}

.header__form-desktop input {
  width: 150px;
  margin-left: 10px;
  padding: 5px;

  border: none;

  &::placeholder {
    color: $color-grey;
  }

  &:hover,
  &:focus {
    border-radius: 3px;
    outline: solid 2px $color-brown;
  }
}

.header__logo {
  display: flex;
  justify-content: center;
}

.header__logo--login {
  width: 66px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.header__form-tablet {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    width: 100%;
  }
}

.header__form-tablet form {
  width: 100%;
}

.header__form-tablet label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0 6px 0 12px;

  background: rgba($color-black, 0.15);
  border-radius: 21px;

  &:hover {
    color: $color-brown;
  }
}

.header__form-tablet input {
  width: 100%;
  margin-left: 10px;
  padding: 5px 10px;

  background: none;
  border: none;

  &:hover,
  &:focus {
    border-radius: 15px;
    outline: solid 2px $color-brown;
  }
}
