.accordion {
  position: relative;
}

.accordion__button.is-js {
  cursor: pointer;

  &:hover {
    color: $color-brown;
  }

  &::after {
    content: "";
    position: absolute;
    top: 17px;
    right: 49px;

    width: 15px;
    height: 8px;

    background-image: url("../img/svg/arrow-down.svg");
    background-repeat: no-repeat;
    background-position: center;

    @media (max-width: $tablet-width) {
      background-image: url("../img/svg/arrow-down-tablet.svg");
      width: 20px;
      height: 15px;
      top: 14px;
      right: 51px;
    }
  }
}

.accordion__item.is-active > .accordion__content {
  display: flex;
}

.accordion__content.is-hidden {
  display: none;
  overflow: hidden;
}

.accordion__item.is-active .accordion__button {
  &::after {
    transform: rotate(-180deg);
  }
}
