.faq {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 970px;
  margin: 0 auto;
  padding-top: 90px;
  padding-bottom: 30px;

  @media (max-width: $tablet-width) {
    padding-top: 39px;
  }
}

.faq__title {
  margin: 0;
  margin-bottom: 49px;

  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  font-family: $font-family--cormorant-garamond;
  text-align: center;
  color: $color-special-black;
  letter-spacing: 0.03em;
}

.faq__list {
  display: grid;
  width: 100%;
  margin: 0;
  padding: 0;

  list-style: none;
  font-style: normal;
  font-family: $font-family--poppins;
  color: $color-special-black;

  border: 1px solid $color-brown;

  grid-template-columns: 1fr;
}

.faq__item {
  padding: 35px 0;

  border-bottom: 1px solid $color-brown;

  &:last-child {
    border-bottom: none;
  }
}

.faq__question {
  position: relative;

  width: 100%;
  margin: 0;
  padding: 9px 42px;

  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  text-align: left;

  background-color: transparent;
  border: none;

  @media (max-width: $tablet-width) {
    padding: 9px 49px;
  }
}

.faq__question--open {
  &::after {
    position: absolute;
  }
}

.faq__question--close {
  &::after {
    position: absolute;

    transform: rotate(-180deg);
  }
}

.faq__answer {
  max-width: 527px;
  margin: 0 0 7px 49px;

  font-weight: 400;
  font-size: 16px;
  line-height: 20px;

  @media (max-width: $tablet-width) {
    max-width: 530px;
    margin-bottom: 10px;
    margin-top: 18px;
  }
}
